import * as React from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { useMutation, useQuery, useQueryClient } from '@tanstack/react-query';

import { alpha, styled } from '@mui/material/styles';
import Autocomplete from '@mui/material/Autocomplete';
import TextField from '@mui/material/TextField';
import Tooltip from '@mui/material/Tooltip';
import TableCell from '@mui/material/TableCell';
import MenuItem from '@mui/material/MenuItem';
import Box from '@mui/material/Box';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TablePagination from '@mui/material/TablePagination';
import TableRow from '@mui/material/TableRow';
import TableSortLabel from '@mui/material/TableSortLabel';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Grid';
import IconButton from '@mui/material/IconButton';
import Avatar from '@mui/material/Avatar';
import LocationOnIcon from '@mui/icons-material/LocationOn';
import AddIcon from '@mui/icons-material/Add';
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';
import InputLabel from '@mui/material/InputLabel';
import Select from '@mui/material/Select';
import FormHelperText from '@mui/material/FormHelperText';
import FormControl from '@mui/material/FormControl';
import FilterListIcon from '@mui/icons-material/FilterList';
import FilterListOffIcon from '@mui/icons-material/FilterListOff';
import ContentCopy from '@mui/icons-material/ContentCopy';
import AccessTime from '@mui/icons-material/AccessTime';
import LocalShipping from '@mui/icons-material/LocalShipping';
import { visuallyHidden } from '@mui/utils';
import { debounce } from '@mui/material/utils';
import { grey, green, orange } from '@mui/material/colors';

import ReserveTruckButton from './reserveTruckButton';

import Loader from '../components/Loader';
import { LocationsQuery, TrucksQuery, TruckDelete } from '../api';
import { useMap, useSnackbar } from '../hooks';
import {
  Order,
  Truck,
  Location,
  Result,
  TruckStatuses,
} from '../utils/dataTypes';
import { getPath, getType, modifyedQueryFn } from '../utils/queryHelpers';
import { stopPropagation } from '../utils/stopPropagation';

interface HeadCell {
  id: string;
  disablePadding: boolean;
  orderField?: keyof Truck;
  getData: (
    row: Truck,
    setMapData: any,
  ) => { data: JSX.Element | string; title?: string };
  label: string;
  numeric: boolean;
}

const headCells: readonly HeadCell[] = [
  {
    id: 'truckNumber',
    orderField: 'truckNumber',
    getData: (row) => ({ data: `${row.truckNumber}` }),
    numeric: true,
    disablePadding: false,
    label: 'Truck Number',
  },
  {
    id: 'mainContactInfo',
    getData: (row) => {
      const person = row.coordinator || row.owner;
      let path = '';
      let type = '';
      let firstRow = '';
      let secondRow = '';
      if (person) {
        path = `./${getPath(person)}/view/${person.id}`;
        type = `${getType(person)} `;
        firstRow = `${person.fullName}`;
        secondRow = `${person?.phone}`;
      }
      return {
        data: (
          <span style={{ display: 'flex' }}>
            <span
              style={{
                textOverflow: 'ellipsis',
                overflow: 'hidden',
                textAlign: 'left',
              }}
            >
              <b>{type}</b>
              <a href={path} target="_blank" onClick={stopPropagation}>
                {firstRow}
              </a>
              <br />
              <a
                href={secondRow ? `tel:${secondRow}` : undefined}
                target="_blank"
                onClick={stopPropagation}
              >
                {secondRow || ' '}
              </a>
            </span>
            <IconButton
              size="small"
              onClick={(event) => {
                stopPropagation(event);
                navigator.clipboard.writeText(firstRow + ' ' + secondRow);
              }}
            >
              <ContentCopy sx={{ height: '16px', width: '16px' }} />
            </IconButton>
          </span>
        ),
        title: type + '' + firstRow + '\n' + secondRow,
      };
    },
    numeric: false,
    disablePadding: true,
    label: 'Main Contact info',
  },
  {
    id: 'truckInfo',
    getData: (row) => {
      const firstRow = row.type ? `${row.type}` : '';
      const secondRow = row.equipment ? `${row.equipment.join(', ')}` : '';

      return {
        data: (
          <span>
            {firstRow}
            <br />
            {secondRow}
          </span>
        ),
        title: firstRow + '\n' + secondRow,
      };
    },
    numeric: false,
    disablePadding: true,
    label: 'Truck info',
  },
  {
    id: 'dimsAndPayload',
    getData: (row) => {
      const firstRowPart1 = row.insideDims ? `${row.insideDims}` : '';
      const firstRowPart2 = row.doorDims ? `${row.doorDims}` : '';
      const secondRow = row.payload ? `${row.payload}` : '';

      return {
        data: (
          <span>
            {(firstRowPart1 ? firstRowPart1 + ' ' : '') + firstRowPart2}
            <br />
            {secondRow}
          </span>
        ),
        title:
          (firstRowPart1 ? firstRowPart1 + ' Inside ' : '') +
          (firstRowPart2 ? firstRowPart2 + ' Door ' : '') +
          '\n' +
          secondRow +
          ' Payload',
      };
    },
    numeric: false,
    disablePadding: true,
    label: 'DIMS/Payload',
  },
  {
    id: 'status',
    orderField: 'status',
    getData: (row) => {
      const status = `${row.status}`;
      const val = (() => {
        switch (status) {
          case 'Will be available':
            return (
              <AccessTime
                sx={{ height: '24px', width: '24px', color: grey[900] }}
              />
            );
          case 'On route':
            return (
              <LocalShipping
                sx={{ height: '24px', width: '24px', color: orange[500] }}
              />
            );
          case 'Available':
            return (
              <Avatar
                sx={{
                  height: '24px',
                  width: '24px',
                  bgcolor: green[500],
                  margin: 'auto',
                }}
              >
                A
              </Avatar>
            );
          case 'Not Available':
            return (
              <Avatar
                sx={{
                  height: '24px',
                  width: '24px',
                  bgcolor: grey[500],
                  margin: 'auto',
                }}
              >
                N
              </Avatar>
            );
          default:
            return status;
        }
      })();

      return {
        data: <span>{val}</span>,
        title: status,
      };
    },
    numeric: false,
    disablePadding: true,
    label: 'Status',
  },
  {
    id: 'roadsDistance',
    getData: (row) => ({
      data: `${row.milesByRoads ? row.milesByRoads.toFixed(2) : ''}`,
    }),
    numeric: false,
    disablePadding: true,
    label: 'Road distance',
  },
  {
    id: 'location',
    getData: (row, setMapData) => {
      const onRefClick = (event: React.SyntheticEvent) => {
        event.preventDefault();
        stopPropagation(event);
        setMapData([row]);
      };

      if (row?.lastCity) {
        const path = `./locations/view/${row.lastCity.id}`;
        const firstRow = `${row.lastCity.name}, ${row.lastCity.stateCode}, ${row.lastCity.zipCode}`;
        const secondRow = `${
          row.locationUpdatedAt
            ? new Date(row.locationUpdatedAt).toLocaleString()
            : ''
        }`;

        return {
          data: (
            <span style={{ display: 'flex' }}>
              <span
                style={{
                  textOverflow: 'ellipsis',
                  overflow: 'hidden',
                }}
              >
                <a href={path} target="_blank" onClick={onRefClick}>
                  {firstRow}
                </a>
                <br />
                {secondRow}
              </span>
              <IconButton
                size="small"
                onClick={(event) => {
                  stopPropagation(event);
                  navigator.clipboard.writeText(firstRow);
                }}
              >
                <ContentCopy sx={{ height: '16px', width: '16px' }} />
              </IconButton>
            </span>
          ),
          title: firstRow + '\n' + secondRow,
        };
      } else {
        return { data: '' };
      }
    },
    numeric: false,
    disablePadding: true,
    label: 'Last known location',
  },
  {
    id: 'calcDistance',
    getData: (row) => ({
      data: `${row.milesHaversine ? row.milesHaversine.toFixed(2) : ''}`,
    }),
    numeric: false,
    disablePadding: true,
    label: 'Origin away',
  },
];

interface EnhancedTableProps {
  numSelected: number;
  onRequestSort: (
    event: React.MouseEvent<unknown>,
    property: keyof Truck,
  ) => void;
  onSelectAllClick: (event: React.ChangeEvent<HTMLInputElement>) => void;
  order: Order | undefined;
  orderBy: string | undefined;
  rowCount: number;
}

const StyledTableHead = styled(TableHead)(({ theme }) => ({
  backgroundColor: theme.palette.background.default,
}));

const StyledTableCell = styled(TableCell)(({ theme }) => ({
  maxWidth: '100px',
  paddingTop: '6px',
  paddingBottom: '6px',
  whiteSpace: 'nowrap',
  textOverflow: 'ellipsis',
  overflow: 'hidden',
}));

function EnhancedTableHead(props: EnhancedTableProps) {
  const {
    onSelectAllClick,
    order,
    orderBy,
    numSelected,
    rowCount,
    onRequestSort,
  } = props;
  const createSortHandler =
    (property: keyof Truck) => (event: React.MouseEvent<unknown>) => {
      onRequestSort(event, property);
    };

  return (
    <StyledTableHead>
      <TableRow>
        <StyledTableCell align="center" padding="checkbox">
          Actions
        </StyledTableCell>
        {headCells.map((headCell) => (
          <StyledTableCell
            key={headCell.id}
            align={headCell.numeric ? 'right' : 'center'}
            padding={headCell.disablePadding ? 'none' : 'normal'}
            sortDirection={orderBy === headCell.id ? order : false}
          >
            <TableSortLabel
              active={headCell.orderField && orderBy === headCell.orderField}
              hideSortIcon={!headCell.orderField}
              direction={orderBy === headCell.orderField ? order : 'asc'}
              onClick={
                headCell.orderField
                  ? createSortHandler(headCell.orderField)
                  : () => {
                      return;
                    }
              }
            >
              {headCell.label}
              {headCell.orderField && orderBy === headCell.orderField ? (
                <Box component="span" sx={visuallyHidden}>
                  {order === 'desc' ? 'sorted descending' : 'sorted ascending'}
                </Box>
              ) : null}
            </TableSortLabel>
          </StyledTableCell>
        ))}
      </TableRow>
    </StyledTableHead>
  );
}

interface EnhancedTableToolbarProps {
  selected: readonly string[];
  setQueryParams: React.Dispatch<
    React.SetStateAction<Record<string, number | string>>
  >;
}

function EnhancedTableToolbar(props: EnhancedTableToolbarProps) {
  const { selected, setQueryParams } = props;
  const [locationValue, setLocationValue] = React.useState<Location | null>(
    null,
  );
  const [inputValue, setInputValue] = React.useState('');
  const [locationsQueryParams, setLocationsQueryParams] = React.useState<{
    search?: any;
  }>({});
  const [options, setOptions] = React.useState<readonly Location[]>([]);
  const [statusValue, setStatusValue] = React.useState<string>('All');
  const [distanceValue, setDistanceValue] = React.useState<number>(200);
  const [searchDataChanged, setSearchDataChanged] =
    React.useState<boolean>(false);
  const location = useLocation();
  const navigate = useNavigate();
  const queryClient = useQueryClient();

  const setSnackbarMessage = useSnackbar();

  const { mutate, isLoading } = useMutation(TruckDelete, {
    onSuccess: () => {
      setSnackbarMessage({
        severity: 'success',
        message: 'Deleted',
      });
      queryClient.invalidateQueries(['trucks']);
    },
    onError: (error: Error) => {
      setSnackbarMessage({
        severity: 'error',
        message: error instanceof Error ? error.message : JSON.stringify(error),
      });
    },
  });

  const setSearch = React.useMemo(() => {
    return debounce((search: string): void => {
      setLocationsQueryParams({ search });
    }, 400);
  }, []);
  const { data: { items } = { items: [], total: 0 } } = useQuery<
    Result<Location>,
    Error,
    Result<Location>,
    any // ToDo fix type
  >({
    queryKey: ['locations', 0, 15, 'name', 'asc', locationsQueryParams],
    queryFn: modifyedQueryFn(LocationsQuery, true),
    keepPreviousData: true,
    retry: false,
  });

  React.useEffect(() => {
    if (locationValue) {
      const notSelectedItems = items.filter(
        (item) => item.id !== locationValue.id,
      );
      setOptions([locationValue, ...notSelectedItems]);
    } else {
      if (inputValue === '') {
        setOptions([]);
      } else {
        setOptions([...items]);
      }
    }
  }, [locationValue, items]);

  const handleLocationChange = (event: any, newValue: Location | null) => {
    setLocationValue(newValue);
    if (newValue?.location) {
      setQueryParams({
        lastLocation: newValue.location.join(','),
        distance: distanceValue,
      });
      setSearchDataChanged(false);
    } else if (!newValue) {
      setInputValue('');
      setQueryParams({});
      setSearchDataChanged(false);
    }
    // setSearchDataChanged(true);
  };
  const handleLocationInputChange = (event: any, newInputValue: string) => {
    setInputValue(newInputValue);
    setSearch(newInputValue);
  };
  const handleStatusInputChange = (
    event: React.ChangeEvent<HTMLInputElement>,
  ) => {
    setStatusValue(event.target.value);
    if (TruckStatuses.some((status) => status === event.target.value)) {
      setQueryParams((prev) => ({ ...prev, status: event.target.value }));
      //setSearchDataChanged(true);
    } else {
      setQueryParams((prev) => {
        const { status: _, ...rest } = prev;
        return { ...rest };
      });
      //setSearchDataChanged(false);
    }
  };
  const handleDistanceInputChange = (
    event: React.ChangeEvent<HTMLInputElement>,
  ) => {
    if (Number.isInteger(Number(event.target.value))) {
      setDistanceValue(Number(event.target.value));
      if (locationValue?.location) {
        setQueryParams({
          lastLocation: locationValue.location.join(','),
          distance: Number(event.target.value),
        });
        setSearchDataChanged(false);
      }
      // setSearchDataChanged(true);
    }
  };
  const handleClearFilter = () => {
    setLocationValue(null);
    setInputValue('');
    setStatusValue('All');
    setDistanceValue(200);
    setQueryParams({});
    setSearchDataChanged(false);
  };
  const handleApplyFilter = () => {
    if (locationValue?.location) {
      setQueryParams({
        lastLocation: locationValue.location.join(','),
        distance: distanceValue,
      });
    } else {
      setQueryParams({});
    }
    setSearchDataChanged(false);
  };
  const createHandler = () => {
    navigate('./create', {
      relative: 'path',
      state: { from: location },
      replace: false,
    });
  };
  const editHandler = () => {
    navigate(`./edit/${selected[0]}`, {
      relative: 'path',
      state: { from: location },
      replace: false,
    });
  };
  const deleteHandler = () => {
    mutate(`${selected[0]}`);
  };
  return (
    <Toolbar
      sx={{
        pl: { sm: 2 },
        pr: { xs: 1, sm: 1 },
        ...(selected.length > 0 && {
          bgcolor: (theme) =>
            alpha(
              theme.palette.primary.main,
              theme.palette.action.activatedOpacity,
            ),
        }),
      }}
    >
      {selected.length > 0 ? (
        <Typography
          sx={{ flex: '1 1 100%' }}
          color="inherit"
          variant="subtitle1"
          component="div"
        >
          {selected.length} selected
        </Typography>
      ) : (
        <Typography
          sx={{
            flex: '1 1 100%',
            whiteSpace: 'nowrap',
            textOverflow: 'ellipsis',
            overflow: 'hidden',
          }}
          variant="h6"
          id="tableTitle"
          component="div"
        >
          Trucks Nearby
        </Typography>
      )}
      <Box
        component="div"
        sx={{
          '& .MuiTextField-root': { m: 1, width: '350px' },
        }}
      >
        <Autocomplete
          getOptionLabel={(option) =>
            typeof option === 'string'
              ? option
              : `${option.name}, ${option.zipCode}, ${option.stateName}`
          }
          filterOptions={(x) => x}
          options={options}
          autoComplete
          includeInputInList
          filterSelectedOptions={false}
          value={locationValue}
          noOptionsText="No locations"
          onChange={handleLocationChange}
          onInputChange={handleLocationInputChange}
          renderInput={(params) => (
            <TextField
              {...params}
              label="Enter city, state or zip"
              size="small"
              fullWidth
            />
          )}
          renderOption={(props, option) => (
            <li {...props}>
              <Grid container alignItems="center">
                <Grid item sx={{ display: 'flex', width: 44 }}>
                  <LocationOnIcon sx={{ color: 'text.secondary' }} />
                </Grid>
                <Grid
                  item
                  sx={{ width: 'calc(100% - 44px)', wordWrap: 'break-word' }}
                >
                  <Box component="span" sx={{ fontWeight: 'regular' }}>
                    {`${option.name}, ${option.stateCode}`}
                  </Box>
                  <Typography variant="body2" color="text.secondary">
                    {`${option.zipCode}, ${option.stateName}`}
                  </Typography>
                </Grid>
              </Grid>
            </li>
          )}
        />
      </Box>
      <Box
        component="div"
        sx={{
          '& .MuiTextField-root': { m: 1, width: '140px' },
        }}
      >
        <TextField
          select
          label="Select Status"
          value={statusValue}
          onChange={handleStatusInputChange}
          size="small"
          fullWidth
        >
          {['All', ...TruckStatuses].map((status) => (
            <MenuItem key={status} value={status}>
              {status}
            </MenuItem>
          ))}
        </TextField>
      </Box>
      <Box
        component="div"
        sx={{
          '& .MuiTextField-root': { m: 1, width: '140px' },
        }}
      >
        <TextField
          select
          label="Select Distance"
          value={distanceValue}
          onChange={handleDistanceInputChange}
          size="small"
          fullWidth
        >
          {Array.from(
            { length: (1000 - 50) / 50 + 1 },
            (_, index) => 50 + index * 50,
          ).map((option) => (
            <MenuItem key={option} value={option}>
              {option}
            </MenuItem>
          ))}
        </TextField>
      </Box>
      <Tooltip title="Filter list">
        {(searchDataChanged || (!locationValue && statusValue === 'All')) &&
        (!(searchDataChanged && !locationValue) ||
          !(searchDataChanged && statusValue === 'All')) ? (
          <span>
            <IconButton
              disabled={!locationValue || !distanceValue}
              onClick={handleApplyFilter}
            >
              <FilterListIcon />
            </IconButton>
          </span>
        ) : (
          <span>
            <IconButton onClick={handleClearFilter}>
              <FilterListOffIcon />
            </IconButton>
          </span>
        )}
      </Tooltip>
      <Loader visible={isLoading} />
    </Toolbar>
  );
}

export default function EnhancedTable() {
  const [order, setOrder] = React.useState<Order | undefined>(undefined);
  const [orderBy, setOrderBy] = React.useState<keyof Truck | undefined>(
    undefined,
  );
  const [selected, setSelected] = React.useState<readonly string[]>([]);
  const [page, setPage] = React.useState<number>(0);
  const [rowsPerPage, setRowsPerPage] = React.useState<number>(15);
  const [queryParams, setQueryParams] = React.useState<Record<string, any>>({});
  const tableContainerRef = React.useRef(null);
  const location = useLocation();
  const navigate = useNavigate();
  const setSnackbarMessage = useSnackbar();
  const [, setMapData] = useMap();

  const {
    isLoading,
    isFetching,
    data: { items, total } = { items: [], total: 0 },
  } = useQuery({
    queryKey: ['trucks', page, rowsPerPage, orderBy, order, queryParams],
    queryFn: TrucksQuery,
    keepPreviousData: true,
    retry: false,
    onError: (error: Error) => {
      setSnackbarMessage({
        severity: 'error',
        message: error instanceof Error ? error.message : JSON.stringify(error),
      });
    },
  });
  const rows = items || [];

  React.useEffect(() => {
    if (tableContainerRef.current) {
      const container = tableContainerRef.current as HTMLElement;
      container.scrollTo({
        top: 0,
        left: 0,
        behavior: 'smooth',
      });
    }
  }, [rows]);

  React.useEffect(() => {
    const newSelected: string[] = [];
    selected.forEach((selectedItem) => {
      const selectedRow = rows.find((row) => row.id === selectedItem);
      if (selectedRow) {
        newSelected.push(selectedItem);
      }
    });
    setSelected(newSelected);
  }, [rows]);

  const handleRequestSort = (
    event: React.MouseEvent<unknown>,
    property: keyof Truck,
  ) => {
    const isAsc = orderBy === property && order === 'asc';
    const isDesc = orderBy === property && order === 'desc';
    if (isDesc) {
      setOrder(undefined);
      setOrderBy(undefined);
    } else {
      setOrder(isAsc ? 'desc' : 'asc');
      setOrderBy(property);
    }
  };

  const handleSelectAllClick = (event: React.ChangeEvent<HTMLInputElement>) => {
    if (event.target.checked) {
      const newSelected = rows.map((n: Truck) => n.id);
      setSelected(newSelected);
      return;
    }
    setSelected([]);
  };

  const handleClick = (
    event: React.MouseEvent<unknown>,
    item: (typeof rows)[number],
  ) => {
    navigate(`./view/${item.id}`, {
      relative: 'path',
      state: { from: location },
      replace: false,
    });
  };

  const handleSelect = (event: React.MouseEvent<unknown>, name: string) => {
    event.stopPropagation();
    // const selectedIndex = selected.indexOf(name);
    // let newSelected: readonly string[] = [];
    //
    // if (selectedIndex === -1) {
    //     newSelected = newSelected.concat(selected, name);
    // } else if (selectedIndex === 0) {
    //     newSelected = newSelected.concat(selected.slice(1));
    // } else if (selectedIndex === selected.length - 1) {
    //     newSelected = newSelected.concat(selected.slice(0, -1));
    // } else if (selectedIndex > 0) {
    //     newSelected = newSelected.concat(
    //         selected.slice(0, selectedIndex),
    //         selected.slice(selectedIndex + 1),
    //     );
    // }
    //
    // setSelected(newSelected);
  };

  const handleChangePage = (event: unknown, newPage: number) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (
    event: React.ChangeEvent<HTMLInputElement>,
  ) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  const isSelected = (name: string) => selected.indexOf(name) !== -1;

  return (
    <Box sx={{ width: '100%', height: '100%' }}>
      <Paper sx={{ width: '100%', height: '100%', backgroundColor: 'unset' }}>
        <EnhancedTableToolbar
          selected={selected}
          setQueryParams={(query) => {
            setQueryParams(query);
            setPage(0);
          }}
        />
        <TableContainer
          ref={tableContainerRef}
          sx={{ height: 'calc(100% - 115px)' }}
        >
          <Table
            sx={{ minWidth: 750 }}
            aria-labelledby="tableTitle"
            size="small"
            stickyHeader
          >
            <EnhancedTableHead
              numSelected={selected.length}
              order={order}
              orderBy={orderBy}
              onSelectAllClick={handleSelectAllClick}
              onRequestSort={handleRequestSort}
              rowCount={rows.length}
            />
            <TableBody>
              {rows.map((row, index) => {
                const isItemSelected = isSelected(`${row.id}`);
                return (
                  <TableRow
                    hover
                    onClick={(event) => handleClick(event, row)}
                    role="checkbox"
                    aria-checked={isItemSelected}
                    tabIndex={-1}
                    key={row.id}
                    selected={isItemSelected}
                    sx={{ cursor: 'pointer' }}
                  >
                    <StyledTableCell padding="checkbox">
                      <ReserveTruckButton truck={row} index={index} />
                    </StyledTableCell>
                    {headCells.map((cell) => {
                      const cellData = cell.getData(row, setMapData);
                      return (
                        <StyledTableCell
                          key={cell.id}
                          title={cellData.title}
                          align={cell.numeric ? 'right' : 'center'}
                        >
                          {cellData.data}
                        </StyledTableCell>
                      );
                    })}
                  </TableRow>
                );
              })}
            </TableBody>
          </Table>
        </TableContainer>
        <TablePagination
          rowsPerPageOptions={[5, 10, 15, 20, 25, 35, 50]}
          component="div"
          count={total}
          rowsPerPage={rowsPerPage}
          page={page}
          onPageChange={handleChangePage}
          onRowsPerPageChange={handleChangeRowsPerPage}
        />
      </Paper>
      <Loader visible={isLoading || isFetching} />
    </Box>
  );
}
